<template>
  <div>
    <div class="page-animate">
      <div class="container-fluid">
        <div class="row justify-content-center">
          <div class="col-md-10">
            <div class="text-input-outer">
              <div class="row">
                <div class="col-md-12">
                  <div class="text-title-group">
                    <label for="text-title"></label>
                    <input
                      id="text-title"
                      type="text"
                      v-model="textTitle"
                      placeholder="--  --"
                    />
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-md-12">
                  <div class="text-nav">
                    <div class="text-img-group">
                      <label for="text-img"> :</label>
                      <select id="text-img" v-model="textImgValue">
                        <option :value="false"></option>
                        <option :value="true"></option>
                      </select>
                    </div>
                    <div class="text-type-group">
                      <label for="text-type-option"> :</label>
                      <select id="text-type-option" v-model="textType">
                        <option :value="null"></option>
                        <option
                          v-for="(type,index) in textTypeItem"
                          :key="index"
                          :value="type.typeNum"
                        >{{type.typeName}}</option>
                      </select>
                    </div>
                    <div class="text-date-group">
                      <span> :</span>
                      <select v-model="year">
                        <option disabled :value="null"></option>
                        <option
                          v-for="(numY,index) in 6"
                          :key="index"
                          :value="`202${index}`"
                        >{{`202${index}`}} </option>
                      </select>
                      <select v-model="month">
                        <option disabled :value="null"></option>
                        <option v-for="(numM,index) in 12" :key="index" :value="numM">{{numM}} </option>
                      </select>
                      <select v-model="dates">
                        <option disabled :value="null"></option>
                        <option v-for="(numD,index) in 31" :key="index" :value="numD">{{numD}} </option>
                      </select>
                    </div>
                  </div>
                  <span
                    class="show-date"
                  >{{year==null?'--':year}} / {{month==null?'--': month}} / {{dates==null?'--':dates}}</span>
                </div>
              </div>
              <div class="row">
                <div class="col-md-12">
                  <div
                    class="text-content-img-group"
                    :class="{'text-content-img-group-active':textImgValue}"
                  >
                    <div
                      class="img-upload-toggle"
                      :class="{'img-upload-toggle-active':textImgValue}"
                    >
                      <div class="img-upload-group">
                        <input type="url" v-model="onlineUrl" @change="uploadImg(type='online')" />
                        <label for="file-upload">
                          
                          <input id="file-upload" type="file" @change="uploadImg(type='local')" />
                        </label>
                      </div>
                      <div class="img-view">
                        <img
                          :class="{'img-show':uploadStatus && textImgValue}"
                          :src="currentUrl"
                          alt
                        />
                        <span></span>
                      </div>
                    </div>
                  </div>
                  <div class="text-content-group">
                    <label for="text-content"></label>
                    <textarea
                      id="text-content"
                      cols="400"
                      rows="15"
                      placeholder="--  --"
                      v-model="descripTextContent"
                    ></textarea>
                    <span> : {{descripTextContent.length}}</span>
                  </div>
                </div>
              </div>
              <div class="text-modify-btn-group-outer">
                <div class="text-modify-btn-group">
                  <span class="back" @click="goBack"></span>
                  <span class="delete" v-if="addStatus == false" @click="deleteArticle"></span>
                  <span class="modify" v-if="addStatus == false" @click="modifyArticle"></span>
                  <span class="add" v-else @click="addArticle"></span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modalC">
      <div class="modal-bodyC">
        <div class="modal-headerC">
          <h2>Title</h2>
          <span class="close">X</span>
        </div>
        <div class="modal-contentC">
          <p>- - This just a demo - -</p>
        </div>
        <div class="modal-footerC">
          <div class="cancel">Cancel</div>
          <div class="confirm">Confirm</div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 0.7s ease;
  .text-input-outer {
    label {
      display: block;
      margin: 5px;
      color: white;
      font-weight: bold;
      text-shadow: 0 0 1px rgba(0, 0, 0, 0.7);
    }
    input[type="text"],
    textarea {
      display: block;
      width: 100%;
      border-radius: 20px;
      outline: none;
      border: none;
      box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
        0 0 2px 0px rgba(0, 0, 0, 0.7);
      text-align: center;
      transition: 0.7s ease;
    }
    input[type="text"]:focus,
    textarea:focus,
    select:focus {
      box-shadow: inset 0 0 2px 1px rgba(0, 60, 255, 0.7),
        0 0 2px 0px rgba(0, 0, 0, 0.7) !important;
    }
    .text-nav {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      .text-img-group {
        display: flex;
        margin: 5px 0 5px 0;
        label {
          display: block;
          margin: 2px 3px 2px 3px;
        }
        select {
          display: block;
          margin: 0 2px 0 2px;
          border-radius: 20px;
          border: none;
          outline: none;
          padding: 3px 5px 3px 5px;
          box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
            0 0 2px 0px rgba(0, 0, 0, 0.7);
          transition: 0.7s ease;
        }
      }
      @media screen and (max-width: 414px) {
        .text-img-group {
          justify-content: flex-end;
          margin: 10px 0 5px 0;
        }
      }
      .text-type-group {
        display: flex;
        margin: 5px 0 5px 0;
        label {
          display: block;
          margin: 2px 3px 2px 3px;
        }
        select {
          display: block;
          margin: 0 2px 0 2px;
          border-radius: 20px;
          border: none;
          outline: none;
          padding: 3px 5px 3px 5px;
          box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
            0 0 2px 0px rgba(0, 0, 0, 0.7);
          transition: 0.7s ease;
        }
      }
      @media screen and (max-width: 414px) {
        .text-type-group {
          justify-content: flex-end;
          margin: 10px 0 5px 0;
        }
      }
      .text-date-group {
        display: flex;
        justify-content: flex-end;
        margin: 10px 0 10px 0;
        span {
          display: block;
          margin: 2px 5px 2px 5px;
          color: white;
          font-weight: bold;
          text-shadow: 0 0 1px rgba(0, 0, 0, 0.7);
        }
        select {
          display: block;
          margin: 0 2px 0 2px;
          border-radius: 20px;
          border: none;
          outline: none;
          padding: 3px 5px 3px 5px;
          box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
            0 0 2px 0px rgba(0, 0, 0, 0.7);
          transition: 0.7s ease;
        }
      }
    }
    @media screen and (max-width: 414px) {
      .text-nav {
        display: block;
      }
    }
    .show-date {
      display: block;
      text-align: right;
      color: white;
      font-weight: bold;
      text-shadow: 0 0 1px rgba(0, 0, 0, 0.7);
    }
    .text-content-img-group {
      overflow: hidden;
      margin: 0 0 0 0;
      transition: 1s ease;
      .img-upload-toggle {
        transition: 1s ease;
        margin-top: -100vh;
        .img-upload-group {
          display: flex;
          align-items: center;
          input[type="file"] {
            display: none;
          }
          input[type="url"] {
            display: block;
            width: 100%;
            border-radius: 20px;
            outline: none;
            border: none;
            box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
              0 0 2px 0px rgba(0, 0, 0, 0.7);
            text-align: center;
            transition: 0.7s ease;
          }
          label {
            display: block;
            width: 30%;
            height: 100%;
            border-radius: 20px;
            background-color: rgb(0, 60, 255);
            box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7),
              0 0 2px 0px rgba(0, 0, 0, 0.7);
            transition: 0.7s ease;
            text-align: center;
            margin: 0 5px 0 5px;
            padding: 1px 0 1px 0;
          }
          input[type="url"]:focus,
          label:focus {
            box-shadow: inset 0 0 2px 1px rgba(0, 60, 255, 0.7),
              0 0 2px 0px rgba(0, 0, 0, 0.7) !important;
          }
        }
        .img-view {
          margin: 10px 3px 3px 3px;
          box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7),
            0 0 3px 1px rgba(0, 0, 0, 0.7);
          border-radius: 15px;
          min-height: 350px;
          overflow: hidden;
          position: relative;
          background-color: rgba(0, 0, 0, 0.7);
          display: flex;
          justify-content: center;
          flex-direction: column;
          align-items: center;
          img {
            opacity: 0;
            transition: 0.7s ease;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
          }
          .img-show {
            opacity: 1;
          }
          span {
            display: block;
            color: white;
            text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
            font-size: 20px;
          }
        }
      }
      .img-upload-toggle-active {
        margin-top: 0;
      }
    }
    .text-content-img-group-active {
      margin: 10px 0 10px 0;
    }
    .text-content-group {
      label {
        display: block;
      }
      span {
        display: block;
        text-align: right;
        color: white;
        font-weight: bold;
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.7);
        margin: 6px 0 6px 0;
        letter-spacing: 1.2px;
      }
    }
    .text-modify-btn-group-outer {
      display: flex;
      justify-content: center;
      margin-top: 30px;
      .text-modify-btn-group {
        display: flex;
        span {
          display: block;
          padding: 3px 20px 3px 20px;
          background-color: rgba(0, 0, 0, 0.7);
          color: white;
          border-radius: 20px;
          margin: 0 10px 0 10px;
          cursor: pointer;
          user-select: none;
        }
      }
    }
  }
}
.page-animate-active {
  opacity: 1;
}
.modalC {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: rgba(0, 0, 0, 0.7);
  .modal-bodyC {
    position: absolute;
    top: 50%;
    left: 50%;
    border: 1px solid black;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
    background: white;
    border-radius: 10px;
    margin: 2% auto;
    max-width: 400px;
    opacity: 0;
    transform: translate(-50%, -100%) scale(0.1);
    transition: 0.5s ease;
    .modal-headerC {
      display: flex;
      justify-content: space-between;
      padding: 15px 15px 10px 15px;
      border-bottom: 1px solid black;
      h2 {
        margin-top: 0px;
        margin-bottom: 0px;
      }
    }
    .modal-contentC {
      padding: 15px;
      text-align: center;
    }
    .close {
      display: block;
      cursor: pointer;
    }
    .modal-footerC {
      display: flex;
      justify-content: flex-end;
      border-top: 1px solid black;
      padding: 10px 0px 10px 5px;
    }
    .cancel,
    .confirm {
      background: gray;
      color: white;
      font-size: 16px;
      margin-right: 5px;
      padding: 5px;
      text-align: center;
      border-radius: 5px;
      cursor: pointer;
      transition: 1s ease;
    }
    .cancel:hover {
      background: red;
      transform: scale(1.05);
      transition: 0.5s ease;
    }
    .confirm:hover {
      background: rgb(0, 162, 255);
      transform: scale(1.05);
      transition: 0.5s ease;
    }
  }
  .modal-body-toggleC {
    opacity: 1;
    transform: translate(-50%, -100%) scale(1);
    transition: 0.5s ease;
  }
}
.modal-toggleC {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      onlineUrl: "",
      localUrl: "",
      currentUrl: "",
      uploadStatus: false,
      textImgValue: false,
      textNum: 0,
      year: null,
      month: null,
      dates: null,
      textTitle: "",
      descripTextContent: "",
      textTypeItem: [
        {
          typeNum: 1,
          typeName: "",
          typeEnName: "potography",
        },
        {
          typeNum: 2,
          typeName: "",
          typeEnName: "psychology",
        },
        {
          typeNum: 3,
          typeName: "",
          typeEnName: "code",
        },
      ],
      textType: null,
      typeGet: null,
      typeNameGet: "",
      idGet: null,
      keyGet: null,
      keyGroup: [],
      showGetArticle: [],
      addStatus: false,
      textTypeTemp: "",
      orderNum: [],
      addNumId: null,
      currentTypeGroup: [],
      currentTypeItem: [],
      currentArticleItem: [],
      getPostIndex: null,
    };
  },
  mounted() {
    setTimeout(
      () =>
        document
          .querySelector(".page-animate")
          .classList.add("page-animate-active"),
      800
    );
  },
  methods: {
    showModal() {
      const modalShow = document.querySelector(".modalC");
      const modalContentShow = document.querySelector(".modal-bodyC");
      modalShow.style.display = "block";
      modalShow.style.transition = "1s ease";
      setTimeout(() => {
        modalShow.classList.add("modal-toggleC");
        modalContentShow.classList.add("modal-body-toggleC");
      }, 100);
    },
    closeModal() {
      const modalShow = document.querySelector(".modalC");
      const modalContentShow = document.querySelector(".modal-bodyC");
      modalShow.classList.remove("modal-toggleC");
      modalContentShow.classList.remove("modal-body-toggleC");
      setTimeout(() => (modalShow.style.display = ""), 750);
    },
    uploadImg(type) {
      type == "online" ? (this.currentUrl = this.onlineUrl) : null;
      this.currentUrl == ""
        ? (this.uploadStatus = false)
        : setTimeout(() => (this.uploadStatus = true), 200);
    },
    goBack() {
      let add = false;
      this.$router.push({ name: "allarticle", params: { add } });
    },
    showArticle() {
      this.showGetArticle.forEach((key) => {
        this.year = key.date.slice(0, 4);
        this.month = key.date.slice(5, 7);
        this.dates = key.date.slice(8, 10);
        this.textType = this.typeGet;
        this.textTitle = key.descripTitle;
        this.descripTextContent = key.descripContent;
      });
      this.textTypeItem.forEach((key) =>
        key.typeNum == this.typeGet ? (this.typeNameGet = key.typeEnName) : null
      );
    },
    getArticle() {
      this.idGet = this.$route.params.id;
      this.typeGet = this.$route.params.type;
      this.getPostIndex = this.$route.params.currentIndexPost;
      this.$route.params.add == true
        ? (this.addStatus = true)
        : (this.addStatus = false);
      this.textTypeItem.forEach((key) => {
        if (key.typeNum == this.typeGet) {
          const unlock = "https://cors-anywhere.herokuapp.com/";
          const url = `https://project-50101.firebaseio.com/${key.typeEnName}.json/`;
          this.axios
            .get(`${unlock}${url}`)
            .then((res) => {
              this.currentTypeGroup.push(res.data);
              this.currentTypeGroup.forEach(
                (key) => (this.keyGroup = Object.keys(key))
              );
              this.currentArticleItem = Object.keys(res.data).map(
                (key) => res.data[key]
              );
              this.currentArticleItem.forEach((key) => {
                if (key.id == this.idGet) {
                  this.keyGet = this.keyGroup[this.getPostIndex];
                  this.showGetArticle.push(key);
                }
              });
              this.showArticle();
            })
            .catch((err) => console.log(err));
        }
      });
    },
    addArticle() {
      this.textTypeItem.forEach((key) => {
        if (key.typeNum == this.textType) {
          const unlock = "https://cors-anywhere.herokuapp.com/";
          const url = `https://project-50101.firebaseio.com/${key.typeEnName}.json/`;
          this.textTypeTemp = key.typeEnName;
          this.axios
            .get(`${unlock}${url}`)
            .then((res) => {
              this.currentTypeItem = Object.keys(res.data).map(
                (key) => res.data[key]
              );
              this.currentTypeItem.forEach((key) => this.orderNum.push(key.id));
              if (this.orderNum == []) {
                this.addNumId = 0;
              } else {
                this.addNumId = Math.max(...this.orderNum);
                this.addNumId += 1;
              }
              setTimeout(() => {
                const unlock = "https://cors-anywhere.herokuapp.com/";
                const url = `https://project-50101.firebaseio.com/${this.textTypeTemp}/.json/`;
                this.axios
                  .post(`${unlock}${url}`, {
                    id: this.addNumId,
                    date: `${this.year}/${this.month}/${this.dates}`,
                    descripContent: this.descripTextContent,
                    descripTitle: this.textTitle,
                    monthNum: this.month,
                    year: this.year,
                  })
                  .then((res) => {
                    setTimeout(() => {
                      this.addNumId = null;
                      this.orderNum = [];
                    }, 300);
                    if (res.status == 200) {
                      alert("");
                      this.$router.push("/dashboard/allarticle");
                    }
                  })
                  .catch((err) => console.log(err));
              }, 300);
            })
            .catch((err) => console.log(err));
        }
      });
    },
    deleteArticle() {
      let ans = null;
      const unlock = "https://cors-anywhere.herokuapp.com/";
      const url = `https://project-50101.firebaseio.com/${this.typeNameGet}/${this.keyGet}/.json/`;
      ans = confirm("");
      ans == true
        ? this.axios
            .put(`${unlock}${url}`, {
              id: "",
              date: "",
              descripContent: "",
              descripTitle: "",
              monthNum: "",
              year: "",
            })
            .then((res) => {
              if (res.status == 200) {
                alert("");
                this.$router.push("/dashboard/allarticle");
              }
            })
            .catch((err) => console.log(err))
        : alert("");
    },
    modifyArticle() {
      let ans = null;
      const unlock = "https://cors-anywhere.herokuapp.com/";
      const url = `https://project-50101.firebaseio.com/${this.typeNameGet}/${this.keyGet}/.json/`;
      this.addNumId = this.idGet;
      ans = confirm("");
      ans == true
        ? this.axios
            .put(`${unlock}${url}`, {
              id: this.addNumId,
              date: `${this.year}/${this.month}/${this.dates}`,
              descripContent: this.descripTextContent,
              descripTitle: this.textTitle,
              monthNum: this.month,
              year: this.year,
            })
            .then((res) => {
              if (res.status == 200) {
                alert("");
                this.$router.push("/dashboard/allarticle");
              }
            })
            .catch((err) => console.log(err))
        : alert("");
    },
  },
  created() {
    this.getArticle();
  },
};
</script>